.main-cards {
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  overflow: visible;
  height: calc(100vh - 370px);
}

.cards-wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 100%;
}

.card {
  color: white;
  position: relative;
  padding: 2px;
  width: 48%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-size: contain;
  transition: all 1s;
  z-index: 2;
}

.card p {
  text-align: center;
  margin: unset;
}

.card::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.363);
  z-index: -12;
}

.card a,
.card a:active,
.card a:visited {
  color: white;
  text-decoration: unset;
}

.card h2 {
  margin-top: 40px;
  cursor: pointer;
}

@media (max-width: 854px) {
  .container-fluid {
    margin-top: 177px;
  }
  .card h2 {
    font-size: 18px;
  }
}
