*,
html,
body {
  margin: 0;
  padding: 0;
  direction: rtl;
  font-family: 'Secular One', sans-serif;
  box-sizing: border-box;
}

.App {
  text-align: center;
}

header {
  width: 100%;
  height: 48px;
  background-color: rgb(137, 190, 0);
  color: rgb(255, 255, 255);
  text-align: center;
}

header h1 {
  font-size: 36px;
  font-weight: 600;
  font-family: 'oswald', sans-serif;
  display: inline-block;
}

main {
  display: flex;
  flex-wrap: wrap;
  height: calc(100vh - 96px);
}

.contact {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
}

.contact h2 {
  width: 90%;
  font-size: 36px;
  font-weight: 600;
  line-height: 4rem;
  letter-spacing: 2px;
  color: #6c757d;
}

.contact i {
  font-size: 30px;
  color: #6c757d;
  width: 100%;
  line-height: 4rem;
}

section {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: auto;
}

section:nth-child(1) {
  background-color: rgb(241, 248, 229);
}

section:nth-child(2) {
  justify-content: space-evenly;
  background-image: url(barca.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

section ul {
  font-family: 'oswald', sans-serif;
  list-style: none;
  width: 420px;
  font-size: 20px;
}

section ul li {
  display: flex;
  padding: 20px 10px;
  color: rgb(0, 0, 0);
  margin: 5px 0;
  transition: all 0.3s;
  cursor: pointer;
}

section ul a {
  text-decoration: none;
}

.number {
  width: 80px;
  margin-right: 20px;
}
.subject {
  width: 170px;
}

a:nth-child(1) li {
  background: rgb(154, 206, 52);
}
a:nth-child(2) li {
  background: rgba(155, 202, 60, 0.795);
}
a:nth-child(3) li {
  background: rgba(161, 202, 78, 0.733);
}
a:nth-child(4) li {
  background: rgba(174, 212, 96, 0.712);
  /* color: black; */
}
a:nth-child(5) li {
  background: rgba(194, 221, 136, 0.685);
  /* color: black; */
}
a:nth-child(6) li {
  background: rgba(204, 226, 159, 0.541);
  /* color: black; */
}

section ul li:hover {
  background: rgb(144, 192, 48);
  transform: scale(1.1);
}

img {
  width: 50%;
}

.social {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.301);
}

.social a {
  text-decoration: none;
  font-size: 22px;
  color: rgb(255, 255, 255);
  display: block;
  transition: all 0.3s ease-out;
  position: relative;
}
.social a:hover {
  color: rgb(176, 233, 62);
}

.social a::after {
  position: absolute;
  left: 0;
  width: 100%;
  height: 3px;
  background: white;
  transition: all 0.3s;
  content: '';
  opacity: 0;
}

.social a::after {
  bottom: 0px;
  transform: translateY(10px);
}

.social a:hover::after {
  opacity: 1;
  transform: translateY(0px);
}

footer {
  width: 100%;
  height: 48px;
  background-color: rgb(137, 190, 0);
  color: rgb(255, 255, 255);
}

@media (max-width: 855px) {
  main {
    flex-direction: column;
    height: 1300px;
  }
}

@media (max-width: 500px) {
  section ul {
    width: 300px;
    font-size: 17px;
  }

  section ul li {
    padding: 12px 6px;
  }

  .contact h2 {
    font-size: 20px;
  }
  .number {
    margin-right: 0;
  }

  section ul li:hover {
    background: rgb(144, 192, 48);
    transform: scale(1);
  }

  .backToHome {
    width: 90%;
  }
}

.summary {
  width: 80%;
  text-align: center;
}

.summary p {
  font-size: 19px;
  text-align: justify;
}

.summary ul {
  margin: 0 auto;
}

.dialog-enter {
  opacity: 0.01;
  transform: scale(1.1);
}

.dialog-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: all 300ms;
}

.dialog-exit {
  opacity: 1;
  transform: scale(1);
}

.dialog-exit-active {
  opacity: 0.01;
  transform: scale(1.1);
  transition: all 300ms;
}

.dropbtn {
  color: white;
  padding: 5px 26px;
  font-size: 19px;
  border: none;
  cursor: pointer;
}

@media (max-width: 500px) {
  .dropbtn {
    display: none;
  }
}

.dropdown {
  position: absolute;
  top: 100px;
  right: 43%;
  display: inline-block;
  /* margin: 0 120px; */
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: white;
  z-index: 1;
  width: 270px;
}
.dropdown-content a {
  color: black;
  padding: 5px 26px;
  text-decoration: none;
  display: block;
}

.dropdown-content a:hover {
  background-color: rgb(214, 214, 214);
}

.dropdown:hover .dropdown-content {
  display: block;
}

ul.none-styled {
  all: unset;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
li.none-styled-li {
  all: unset;
}
